import React from 'react'
import {bindActionCreators} from 'redux'
import {connect} from 'react-redux'
import {entries} from 'reducer'
import {museumActions} from 'action'
import {BackHome} from 'components'
import './index.scss'

interface P {
    className?: string,
    history: any,
    match: any,
    getAdsInfoDetail: Function,
    adsInfoDetail: any
}

interface S {

}

class AdsInfoDetail extends React.Component<P, S> {
    // 构造
    constructor(props: any) {
        super(props);
        // 初始状态
        this.state = {};
    }

    render(): React.ReactElement<any, string | React.JSXElementConstructor<any>> | string | number | {} | React.ReactNodeArray | React.ReactPortal | boolean | null | undefined {
        const {
            props: {
                className = '',
                adsInfoDetail
            },
            state: {}
        } = this
        return (
            <div className={'ads_info_detail'}>
                <div className={'content'} dangerouslySetInnerHTML={{__html: adsInfoDetail.content}} />
                <BackHome />
            </div>
        );
    }

    componentDidMount(): void {
        this.getInit()
    }

    getInit = async () => {
        const {match: {params: {id}}, getAdsInfoDetail} = this.props
        const {title} = await getAdsInfoDetail(id)
        document.title = title
    }

    shouldComponentUpdate(nextProps: Readonly<any>, nextState: Readonly<any>, nextContext: any): boolean {
        return true
    }
}


function mapStateToProps(state: entries) {
    const {adsInfoDetail} = state.museumReducer
    return {adsInfoDetail}
}

function mapDispatchToProps(dispatch: any) {
    const {getAdsInfoDetail} = museumActions
    return {
        ...bindActionCreators({
            getAdsInfoDetail
        }, dispatch),
        dispatch
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(AdsInfoDetail)
